<template>
	<div class="limiter">
		<div class="container-login100">
			<div class="wrap-login100">
				<div class="login100-pic js-tilt" data-tilt>
					<img src="./images/img-01.png" alt="IMG">
				</div>
				<form class="login100-form validate-form">
					<span class="login100-form-title">
						用户登录
					</span>
					<div class="wrap-input100 validate-input" data-validate = "Valid email is required: ex@abc.xyz">
						<input class="input100" type="text" name="email" v-model="username" placeholder="账号">
						<span class="focus-input100"></span>
						<span class="symbol-input100">
							<i class="fa fa-envelope" aria-hidden="true"></i>
						</span>
					</div>
					<div class="wrap-input100 validate-input" data-validate = "Password is required">
						<input class="input100" type="password" name="pass" v-model="password" placeholder="密码">
						<span class="focus-input100"></span>
						<span class="symbol-input100">
							<i class="fa fa-lock" aria-hidden="true"></i>
						</span>
					</div>
					
					<div class="container-login100-form-btn">
						<button class="login100-form-btn" type="button" @click="login">
							登录
						</button>
					</div>
					<div class="text-center p-t-12">
						<!-- <span class="txt1">
							Forgot
						</span>
						<a class="txt2" href="#">
							Username / Password?
						</a> -->
					</div>
					<div class="text-center p-t-136">
						<!-- <a class="txt2" href="#">
							Create your Account
							<i class="fa fa-long-arrow-right m-l-5" aria-hidden="true"></i>
						</a> -->
					</div>
				</form>
			</div>
		</div>
	</div>
</template>

<script>
export default {
    data() {
        return {
            username: '',
            password: ''
        }
    },
    mounted() {
    },
    methods: {
        login() {
            if(!this.username) {
                this.$Message.info("请输入用户名!");
                return 
            }
            if(!this.password) {
                this.$Message.info("请输入密码!");
                return 
            }
            let info = '?username=' + this.username + '&password=' + this.password
            common.get('login' + info).then(res => {
				this.$set(common, 'username', this.username)
				
				this.$set(common, 'isManager', res.manager)
				localStorage.setItem('username', this.username)
				localStorage.setItem('isManager', res.manager)
                this.$Message.success("登录成功");
                this.$router.replace({
                    path: '/'
                })
            }).catch(error => {
                this.$Message.info("账号密码不正确，请重新输入!");
            })
        }

    }
}
</script>

<style lang="css" scoped>
@import './vendor/bootstrap/css/bootstrap.css';
@import './vendor/animate/animate.css';
@import './vendor/css-hamburgers/hamburgers.min.css';
@import './css/util.css';
@import './css/main.css';
</style>
